export default {
	data() {
		return {
			searchName: '',
			statusPicker: 0,
			searchResult: [],
			staffList: null
		};
	},
	onLoad(){
		
		this.GetStaffList()
	},
	watch: {
		searchName(nv, ov){
			this.searchResult = []
			if(nv != ''){
				
				for(var i in this.staffList){
					console.log(this.staffList[i].name);
					if(this.staffList[i].name.indexOf(nv) != -1){
						var data = {
							name: this.staffList[i].name,
							id: this.staffList[i].id
						}
						this.searchResult.push(data)
						console.log(this.searchResult);
					}
					
				}
			}
			
		}
	},
	methods:{
		viewStaff(index){
			this.searchResult
			uni.navigateTo({
				url: "../people-detail/people-detail?id=" + this.searchResult[index].id
			})
		},
		GetStaffList(){
			uni.showLoading({
				title: '请稍等'
			})
			var that = this
			uni.request({
				header: {token: that.Token},
				url: that.Server + 'staff/list',
				data: {
					merchantId: that.MerchantId
				},
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						that.staffList = res.data.data
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
		},
		search(){
			
		},
		PickerChangeStatus(){
			
		}
	}
}
